<template>
	<div class="login">
		<div class="signin">
			<div style="width: 138px; margin: 0px auto;">
				<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px"
					y="0px" viewBox="0 0 200 66.92" style="enable-background: new 0 0 200 66.92;" xml:space="preserve">
					<image style="overflow: visible;" width="1772" height="506"
						transform="matrix(0.1071 0 0 0.1071 5.1307 5.6736)" xlink:href="@/assets/Definer_logo.png"></image>
				</svg>
			</div>
			<p style="color: black;padding-bottom:20px;">
				Select the preferences you want to edit for project
			</p>

			<div class="table_wrapper" style="height: 212px;width:100%;">
				<el-table class="table" border :data="list" size="small" height="100%">
					<el-table-column prop="preference_name" label="Name" width="170.33"></el-table-column>
					<el-table-column prop="last_edit" label="Last Edit" width="170.33">
						<template slot-scope="scope">
							{{ formatTimeAgo(scope.row.last_edit) }}
						</template>
					</el-table-column>
					<el-table-column :label="lang.table.dost" width="170.33">
						<template slot-scope="scope">
							<el-button type="text" icon="el-icon-edit" @click="goEdit(scope.row.group_id)"
								style="background-color: blue; padding: 8px; border-radius: 8px; color: white;"></el-button>
							<el-button type="text" icon="el-icon-delete" @click="del(scope.row.preference_name)"
								style="background-color: red; padding: 8px; border-radius: 8px; color: white;"></el-button>
							<el-button type="text" icon="el-icon-view" @click="goView(scope.row.group_id)"
								style="color: black;"></el-button>
						</template>
					</el-table-column>

				</el-table>
			</div>
			<el-button class="submit" type="primary" style="margin-top:30px;" :loading="submitting"
				@click="addNewPreference">
				Add new preference
			</el-button>
		</div>
	</div>
</template>
  
<script>
import timer from "../tools/timer";
import global from "../tools/global";
export default {
	name: "team",
	data() {
		return {
			key: "",
			list: [],
			count: 0,
			page: 1,
			loading: false,
			showAdd: false,
			editingRow: null,
			selectedId: null,
			preference_name: '',
			showDateTimePicker: false, // Track visibility of the DateTime picker
			selectedDateTime: "",

			selectedSubscription: "",
			addForm: {
				name: "",
				role: "2",
			},
		};
	},
	created() {
		this.lang = this.$lang("team");
	},
	mounted() {
		this.getList();
	},
	methods: {
		// Get data list
		getList() {
			this.loading = true;
			this.$ajax.get('/project/list_preferences/?id=' + this.$route.query.id).then((result) => {
				console.log(result);
				this.list = result;
				this.loading = false;
			});
		},
		goEdit(group_id) {
			this.$router.push({ path: '/u.edit.library', query: { group_id } });
		},
		goView(group_id) {
			this.$router.push({ path: '/u.view.library', query: { group_id } });
		},
		addNewPreference() {
			this.$router.push({ path: '/u.library', query: this.$route.query });
		},
		formatTimeAgo(lastEdit) {
			const lastEditTime = new Date(lastEdit);
			const currentTime = new Date();
			const timeDifference = currentTime - lastEditTime;

			// Define time units in milliseconds
			const second = 1000;
			const minute = second * 60;
			const hour = minute * 60;
			const day = hour * 24;
			const month = day * 30;

			if (timeDifference < minute) {
				const secondsAgo = Math.floor(timeDifference / second);
				return `${secondsAgo} seconds ago`;
			} else if (timeDifference < hour) {
				const minutesAgo = Math.floor(timeDifference / minute);
				return `${minutesAgo} minutes ago`;
			} else if (timeDifference < day) {
				const hoursAgo = Math.floor(timeDifference / hour);
				return `${hoursAgo} hours ago`;
			} else if (timeDifference < month) {
				const daysAgo = Math.floor(timeDifference / day);
				return `${daysAgo} days ago`;
			} else {
				const monthsAgo = Math.floor(timeDifference / month);
				return `${monthsAgo} months ago`;
			}
		},
		// 删除项目
		del(preference_name) {
			let that = this;
			this.$confirm(
				`Are you sure you want to delete '${preference_name}'?`,
				'Confirmation',
				{
					closeOnPressEscape: false,
					closeOnClickModal: false,
					cancelButtonText: 'No, cancel',
					confirmButtonText: 'Yes, delete',
					showCancelButton: true,
					showConfirmButton: true,
					customClass: 'custom-confirm-dialog',
					beforeClose(action, instance, done) {
						if (action == 'confirm') {
							if (that.httping) return;
							that.httping = true;
							let index = 0;
							that.list.some((item, i) => {
								if (item.preference_name == preference_name) {
									index = i;
									return true;
								}
							});
							that.$ajax
								.get('/group/del_preference_name/?preference_name=' + preference_name)
								.then(r => {
									that.list.splice(index, 1);
									that.$message.success('success');
									done();
									setTimeout(() => {
										that.httping = false;
									}, 300);
									// Update the user's projects and trigger computed property update
									// that.updateUserProjects();
								});
						} else {
							done();
						}
					}
				}
			).catch(() => { });
		}


	},
};
</script>
  

<style lang="less" scoped>
@import url('../tools/common.less');

.login {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	/* Horizontally center content */
	align-items: center;
	/* Vertically center content */
	position: absolute;
	background-color: #355CA1;
}

.signin {
	width: 512px;
	background-color: #FFFFFF;
	border-radius: 24px;
	padding: 30px;

	p {
		color: #86868B;
		text-align: center;
		margin-top: 12px;
	}

	.submit {
		width: 200px;
		margin: 10px auto;
		background-color: #345CA0;
		display: block;
	}
}</style>
